<template>
  <div v-if="paperStyle === 'chip'" ref="header" :index="card.index"
    :class="{ animate: !isDragging, 'front-most': isDragging }"
    class="paper-card caption"
    :style="cardStyle"
    style="background: rgba(255, 255, 255, 0.8); border-radius: 8px; border-style: solid; border-width: 1px; display: flex; overflow: hidden;"
    @mousedown.stop="dragElement"
    @mouseover="emitPaperId('mouseoverrefcount', 'mouseovercitecount')"
    @mouseout="emitPaperId('mouseoutrefcount', 'mouseoutcitecount')"
    @click="emitPaperId('clickrefcount', 'clickcitecount')">
    <div :style="citingStyle"></div>
    <div style="flex-grow: 1; min-width: 0;" class="py-1 px-2">
      <div class="text-xs-center font-weight-bold">
        {{ card.paper.authors[0].family.slice(0, 10) }} {{ card.paper.year }}
      </div>
      <div class="text-xs-center text-truncate">{{ card.paper.title }}</div>
      <div class="text-xs-center" style="display: flex;"><span class="text-truncate" style="flex: 1 1 25px;">{{ card.paper.venue.name }}</span><span class="mx-2">-</span><span>Cited by {{ card.paper.citedByCount }}</span></div>
    </div>
    <div :style="citedByStyle"></div>
  </div>
  <transition v-else name="background">
    <div class="paper-card"
      v-bind:index="card.index"
      v-bind:class="{ animate: !isDragging, 'front-most': isDragging }"
      v-bind:style="cardStyle">
      <v-hover close-delay=0>
        <v-card slot-scope="{ hover }"
          :class="`elevation-${hover ? 12 : 2}`">
          <v-system-bar status ref="header" :style="{ padding: '0px', 'background-color': card.isSelected ? 'orange' : undefined }">
            <span class="header-item"
              :style="{ 'background-color': inNetworkReferenceColor }"
              @mouseover="$emit('mouseoverrefcount', card.paper.id)"
              @mouseout="$emit('mouseoutrefcount', card.paper.id)"
              @click="$emit('clickrefcount', card.paper.id)">
              <span>&lt; {{ card.inGraphCitings.length }} / {{ card.paper.references.length }}</span>
            </span>
            <v-spacer style="height: 100%;"
              @mousedown.stop="dragElement">
            </v-spacer>
            <v-icon light @click="$emit('remove', card.index)">close</v-icon>
            <span class="header-item"
              :style="{ 'background-color': inNetworkCitationColor }"
              @mouseover="$emit('mouseovercitecount', card.paper.id)"
              @mouseout="$emit('mouseoutcitecount', card.paper.id)"
              @click="$emit('clickcitecount', card.paper.id)">
              <span>{{ card.inGraphCitedBys.length }} / {{ card.paper.citationCount }} &gt;</span>
            </span>
          </v-system-bar>
          <v-card-text class="caption">
            <h4>
              <a @click="$emit('clicktitle', card.index)">{{ card.paper.title }}</a>
            </h4>
            <div>
              <template v-for="(name, index) in authorNames">
                <span :key="`author-name-${index}`">
                  <a @click="console">{{ name }}</a>
                </span>
                <span v-if="index != authorNames.length - 1" :key="`author-and-${index}`">
                  and
                </span>
              </template>
            </div>
            <div>
              <span><a @click="console">Year {{ card.paper.year }}</a></span>, <span><a @click="console">Referenced {{ card.paper.references.length }}</a></span>, <span><a @click="console">Cited by {{ card.paper.citationCount }}</a></span>
            </div>
          </v-card-text>
        </v-card>
      </v-hover>
    </div>
  </transition>
</template>

<script>
import { create as createRect } from './rect.js'
import _ from 'lodash'
import { Author } from './paper.js'
import { interpolateBuPu as interpolateColor } from 'd3-scale-chromatic'

export default {
  name: 'PaperCard',
  props: {
    card: Object,
    autoHeight: {
      type: Boolean,
      default: true
    },
    draggable: {
      type: Boolean,
      default: true
    },
    paperStyle: {
      type: String,
      default: 'chip'
    }
  },
  data () {
    return {
      isDragging: false,
      rect: { ...this.card.rect }
    }
  },
  computed: {
    paper: function () { return this.card.paper },
    authorNames: function () {
      return _.map(this.paper.authors, author => Author.stringify(author))
    },
    cardStyle: function () {
      const paddingRight = 0
      const style = {
        left: this.rect.left + 'px',
        top: this.rect.top + 'px',
        width: (this.rect.width + paddingRight) + 'px',
        paddingRight: paddingRight + 'px'
      }
      const height = { height: this.rect.height + 'px' }
      return this.autoHeight ? style : { ...style, ...height }
    },
    citingStyle: function () {
      return {
        opacity: 0.8,
        backgroundColor: this.relationColor(this.card.inGraphCitings.length),
        flex: `0 0 6px`
      }
    },
    citedByStyle: function () {
      return {
        opacity: 0.8,
        backgroundColor: this.relationColor(this.card.inGraphCitedBys.length),
        flex: `0 0 6px`
      }
    },
    inNetworkCitationColor: function () {
      return this.getColor({
        maxLevel: 4,
        logBase: 2,
        value: this.card.inGraphCitedBys.length
      })
    },
    inNetworkReferenceColor: function () {
      return this.getColor({
        maxLevel: 4,
        logBase: 2,
        value: this.card.inGraphCitings.length
      })
    }
  },
  watch: {
    card: function (curr, prev) {
      this.rect = createRect(curr.rect)
    }
  },
  methods: {
    relationColor: function (count) {
      const scalar = 1 - Math.exp(Math.log(1) - 0.2 * count)
      return interpolateColor(scalar)
    },
    emitPaperId: function (...evts) {
      _.forEach(evts, evt => {
        this.$emit(evt, this.card.paper.id)
      })
    },
    dragElement: function (evt) {
      if (!this.draggable) {
        return
      }
      evt.preventDefault()
      const xInit = evt.clientX
      const yInit = evt.clientY
      let xPrev = evt.clientX
      let yPrev = evt.clientY
      const closeDragElement = evt => {
        this.isDragging = false
        document.onmouseup = null
        document.onmousemove = null
        const xCurr = evt.clientX
        const yCurr = evt.clientY
        const xDiff = xCurr - xInit
        const yDiff = yCurr - yInit
        if (Math.sqrt(xDiff * xDiff + yDiff * yDiff) > 1) {
          const card = {
            ...this.card,
            rect: createRect(this.rect)
          }
          this.$emit('dragend', card, evt)
        } else {
          this.$emit('clickhandle', this.card.index)
        }
      }
      const elementDrag = evt => {
        const xCurr = evt.clientX
        const yCurr = evt.clientY
        this.rect.left += (xCurr - xPrev)
        this.rect.top += (yCurr - yPrev)
        const card = {
          ...this.card,
          rect: createRect(this.rect)
        }
        this.$emit('update:card', card)
        xPrev = xCurr
        yPrev = yCurr
      }
      this.isDragging = true
      document.onmouseup = closeDragElement
      document.onmousemove = elementDrag
    },
    getColor: function ({ maxLevel, logBase, value }) {
      const log = x => Math.log(x) / Math.log(logBase)
      const count = Math.floor(value)
      const unboundLevel = count === 0 ? 0 : Math.floor(log(count)) + 1
      const boundLevel = Math.min(maxLevel, unboundLevel)
      const r = boundLevel / maxLevel
      const minColor = [50, 50, 50]
      const maxColor = [50, 200, 250]
      const color = []
      for (let i = 0; i < 3; ++i) {
        color[i] = minColor[i] * (1 - r) + maxColor[i] * r
      }
      return `rgb(${color[0]}, ${color[1]}, ${color[2]})`
    },
    console: function (evt) {
      console.log(evt)
    }
  }
}
</script>

<style scoped>
.paper-card {
  position: absolute;
  white-space: normal;
  word-break: break-all;
}

a {
  color: black;
}

a:hover {
  background-color: yellow;
}

.header-item {
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0px 5px;
  color: white;
}

.animate {
  transition-duration: 0.1s;
}

.front-most {
  z-index: 99999;
}

.v-card {
  background: rgba(255, 255, 255, 0.8);
}

.background-enter-active .v-card {
  transition: background-color 2s ease-in;
}
.background-enter .v-card {
  background-color: yellow;
}
</style>



